.hero{		
	background: white;
	margin: 75px auto 115px auto;

	.wrapper {
	margin: 0 auto 0 auto;
	width:100%;
	max-width: 1050px;
	text-align: center;

		h1{
		margin: 15px;
		font-size: 50px;
		font-weight: bold;
		text-transform: uppercase;
		color: #848789;
		}
		p{
		font-size: 16px;
		color: pink;
		line-height:35px;
		}
	}
	.cta {
		padding: 60px 0 60px 0;
		text-align: center;
		background: white;
		a{
			border: 1px solid pink;
			border-radius: 10px;
			line-height: 45px;
			font-size: 14px;
			display: inline-block;
			text-transform: uppercase;
			color: #848789;
			text-decoration: none;
			padding: 0px 20px 0px 20px;
				&:hover {
					border: 1px solid #848789;
					color: pink;
				}
		}
	}
}