nav {
	width: 100%;
	margin: 37px 0 75px 0;
	font-size: 0;
		
		.left{
			width: 70%;
			display: inline-block;
			li{
				display: inline-block;
				text-align: left;
				vertical-align: middle;
				font-family: $primary-font;
				font-size:13px;
				color:#848789;
				margin: 0 25px 0 25px;
				a{
					color:#848789;
					text-decoration: none;
					&:hover {
						color: pink;
					}
				}
				img{
					width: 100px;
				}
				i{
					font-size:50px;
					color: pink;
				}
			}
			
		}
		.right{
			width: 30%;
			display: inline-block;
			text-align: right;
			li{
				display: inline-block;
				text-align: right;
				
				font-family: $primary-font;
				font-size:13px;
				color:#848789;
				margin: 0 25px 0 25px;
				a{
					color:#848789;
					text-decoration: none;
					&:hover {
						color: pink;
					}

					i {
						&.icon-zoom {
							border: 1px solid pink;
							border-radius: 10px;
							padding: 5px;
							color: #848789;
							font-size: 20px;
							&:hover {
							border: 1px solid #848789;
							color: pink;
							}
						}
					}

				}
			}
		}
/**
