				border: 1px solid pink;
				border-radius: 10px;
				padding: 5px;
				color: #848789;
				i {
					font-size: 20px;
				}
				&:hover {
					border: 1px solid #848789;
					color: pink;
				}
			}
		}
**/	
	&:after{
		content:'';
		height: 1px;
		background: pink;
		display: block;
		margin: 30px 35px 30px 35px;
	}
}