.signup{			background: white;	padding: 100px 0;	.wrapper {	width:100%;	max-width: 1050px;	margin: 75px auto 75px auto;	text-align: center;		h1{		margin: 15px;		font-size: 30px;		font-weight: bold;		text-transform: uppercase;		color: #848789;		}		p{		font-size: 14px;		color: #848789;		line-height:35px;		}	}	.cta {		text-align: center;		a{			border: 1px solid pink;			border-radius: 0 10px 10px 0;			margin-left: -4px;			line-height: 43px;			font-size: 14px;			display: inline-block;			color: #848789;			background: pink;			text-decoration: none;			vertical-align: top;			padding: 0px 20px 0px 20px;				&:hover {					border: 1px solid #848789;					background: #848789;					color: pink;				}		}		.input{			line-height: 40px;			width: 475px;			font-size: 17px;			color: pink;			border-radius: 10px 0 0 10px;		}	}}