
.footer{
	left: 0;
   	bottom: 0;
   	width: 1180px;
   	height:150px;
   	margin:45px auto 45px auto;

   		.footer-list{
   				display: inline-block;
   				width: 290px;
   				height: 150px;
   				vertical-align: top;
   				padding: 15px 0 15px 0;
   				position: relative;

   				li{
					text-align: left;
					font-family: $primary-font;
					font-size: 12px;
					color:#848789;
					margin: 5px 0 5px 0;
					font-weight: bold;
					a{
						color:#848789;
						text-decoration: none;
						font-family: $primary-font;
						font-size: 10px;
						font-weight: normal;
						line-height:1.6;
							&:hover {
								color: pink;
							}
					}

						i{
								border: 1px solid pink;
								border-radius: 7px;
								padding: 2px;
								color: #848789;
								font-size: 12px;
								line-height:3;
								margin-right: 5px;
									&:hover {
									border: 1px solid #848789;
									color: pink;
									}
						}	

					}
						&:not(:last-child):after{
							position: absolute;
							width: 1px;
							content: '';
							height: 150px;
							background: pink;
							top: 0;
							right: 0;
							vertical-align: middle;
	   					}
		}	
}