.carousel{
	margin:115px auto 45px auto;
	background: #ffe6ea;
	width:1100px;
	height:120px;
	position: relative;
		.left{		
				display: block;
				width: 40px;
				height: 120px;
				position: absolute;
				background: pink;
				text-decoration: none;
				left: -40px;
				i{
					font-size: 30px;
					color: #ffe6ea;
					position:absolute;
					top:50%;
					left:50%;
					transform:translate(-50%,-50%);
					&.icon-chevron-left {
						&:before {
							position:relative;
							left:2px;
						}
					}
				}

		}
		.right{
				display: block;
				width: 40px;
				height: 120px;
				left:100%;
				position: absolute;
				vertical-align: middle;
				background: pink;
				text-decoration: none;
				i{
					font-size: 30px;
					color: #ffe6ea;
					position: absolute;
					top:50%;
					left:50%;
					transform:translate(-50%,-50%);
				}
		}
}
