.info{
	padding: 50px;
	top:50%;
	position: relative;
	transform:translateY(-50%);
	text-align: center;

		h1{
			margin: 15px;
			font-size: 18px;
			font-weight: bold;
			text-transform: uppercase;
			color: #848789;
			line-height:1.6;
		}
		h2{
			margin: 15px;
			font-size: 12px;
			font-weight: bold;
			text-transform: uppercase;
			font-style: italic;
			color: #848789;
		}
		p{
			font-size: 10px;
			color: #848789;
			line-height:1.6;
			margin-top:15px;
		}
		em{
			font-size: 12px;
			color: #848789;
			line-height:1.6;
			font-style: italic;
			display: block;
		}
		i{
			color: #848789;
			font-size: 40px;
		}
.icon{
	max-width:140px;
	height:50px;
	width: 100%;
	vertical-align: middle;
	position:relative;
	&:before{
				position:absolute;
				top: 50%;
				left: 0;
				width: 25px;
				content:'';
				height: 1px;
				background: #848789;
			}
			&:after{
				position:absolute;
				top: 50%;
				right: 0;
				width: 25px;
				content:'';
				height: 1px;
				background: #848789;
			}

}
}